











import { Component, Prop, Vue } from 'vue-property-decorator';
import NavBar from "@/components/components/NavBar.vue";

@Component({
  components: { NavBar }
})
export default class MainPage extends Vue {
  user = this.$store.state.auth.user

  mounted() {
    if(!this.user.logged) {
      this.$router.push("/")
    }
  }

}
