























import { Component, Prop, Vue } from 'vue-property-decorator';
import Device from "@/components/components/Device.vue";
import authHeader from "@/services/auth-header";
@Component({
  components: {Device}
})
export default class DeviceCategoryLayout extends Vue {
  @Prop() private title!: string;
  @Prop() private categoryid!: number;

  user = this.$store.state.auth.user
  hover = false
  loading = false

  backError = false

  remCategory() {
    this.loading = true

    this.$api.delete('/category/delete/' + this.categoryid,{ headers: authHeader(this.user.token) }).then((res) => {

      this.$api.get('/devices/all').then((res) => {
        this.$store.dispatch('initDevices', res.data)
      }).catch((error) => {
        console.log(error)
      })
      this.loading = true
      setTimeout(() => {
        this.loading = false
        window.location.reload()
      }, 1000)
    }).catch((error) => {
      this.backError = true
    })
  }


}
